.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.dfLogo {
  width: 3rem;
}

.tabBox {
  background-color: #2d3436;
  color: #f1c40f;
}

.momBox {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 15px;
  margin-bottom: 15px;
}

.momImgBox {
  height: 12rem;
}

.fieldContainer {
  position: fixed;
  width: 100%;
}

.fieldImgBox {
  position: absolute;
  width: 100%;
  z-index: 1;
}

.fieldImg {
  width: 100%;
  z-index: 2;
}

.modalContainer {
  width: 100%;
  height: 90vh;
  position: absolute;
  background-color: black;
  z-index: 3;
  opacity: 0.6;
}

.playerContainer {
  width: 100%;
  height: 75vh;
  margin-top: 5vh;
  position: absolute;
  z-index: 3;
  overflow-y: scroll;
}

.statContainer {
  width: 100%;
  height: 80vh;
  position: absolute;
  z-index: 3;
  overflow-y: scroll;
}

.target {
  width: 2rem;
  height: 2rem;
  border-radius: 50%;
  background-color: #f1c40f;
  position: absolute;
  z-index: 2;
  /* display: none; */
}

.closeBox {
  margin-top: 33rem;
  margin-left: 90%;
  padding: 5px;
  width: 2rem;
  height: 2rem;
  border-radius: 5px;
  background-color: white;
  position: fixed;
  z-index: 7;
  font-weight: bold;
}

.playerBox {
  margin-top: 5%;
  margin-left: 20%;
  width: 60%;
  height: 2rem;
  border-radius: 5px;
  background-color: #2d3436;
  color: #f1c40f;
  position: relative;
  z-index: 5;
  font-weight: bold;
  font-size: large;
  padding: 5px;
}

.playerBox:hover {
  background-color: #f1c40f;
  color: #2d3436;
}

.statBox {
  margin-top: 10%;
  margin-left: 20%;
  width: 60%;
  height: 3rem;
  border-radius: 5px;
  background-color: #2d3436;
  color: #f1c40f;
  position: relative;
  z-index: 5;
  font-weight: bold;
  font-size: large;
  padding: 10px;
}

.statBox:hover {
  background-color: #f1c40f;
  color: #2d3436;
}

.statTable {
  white-space: nowrap; 
  overflow-x: auto;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@media (min-width: 1048px) { 
  .mainContainer {
    width: 25rem;
    margin-left: 35%;
  }

  .fieldContainer {
    position: absolute;
    width: 25%;
    margin-left: 37.5%;
  }

}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
